import Button from "../../base/Button";
import style from "../style/LoginPage.css";
import img from "../../base/.jpg";
import Logo from "../../base/Logo";
import axios from "axios";
import { useNavigate } from "react-router";

const RegisterPage = () => {

    // const url = "/api/register";
    // const sendParam = {
    //     userid: "asdsad",
    //     userpassword: "zxc",
    //     usernickname: "asd"
    // }
    //
    // axios.post(url, sendParam)
    //     .then((res) => {
    //         console.log(res)
    //     })
    //     .catch((error) => {
    //         console.log(error.response)
    //     })


    const navigate = useNavigate();
    const data = {
        userid: "user1",
        userpassword: "1234",
        usernickname: "user1"
    };

    const onSubmit = (e) => {
        e.preventDefault();
        axios.post("/api/register/", JSON.stringify(data),{headers:{
                "Content-Type":"application/json"
            }});
        navigate('/');
    };


    return(
        <>
            <div className="loginBlock">
            <div className="whiteBox">
                <Logo />
                <h2></h2>
                <form className="loginForm" onSubmit={onSubmit} action="">
                    <input required type="text" id="id" placeholder=""/>
                    <input required type="text" id="nickname" placeholder=""/>
                    <input required type="password" id="password" placeholder=""/>
                    <Button type="submit"></Button>
                </form>
            </div>
        </div>
        </>
    );
}

export default RegisterPage;