import { Link } from "react-router-dom";
import Header from "../../base/Header";
import CustomCategoryList from "../cloth/CustomCategoryList";
import style from "../style/MainPage.css"

const MainPage = ({mode}) => {
    const categoty = [];
    for(let i = 0; i < 6; i++){
        categoty.push(` ${i}`);
    }
    return(
        <div>
            {mode ? // 
                <div>
                    <Header mode={mode} />
                    <div className="mainWeather">
                        <Link to="/spring"><button></button></Link>
                        <Link to="/summer"><button></button></Link>
                        <Link to="/autumn"><button></button></Link>
                        <Link to="/winter"><button></button></Link>
                    </div>
                    <CustomCategoryList />
                </div>
                : // 
                <div>
                    <Header mode={mode} />
                    <h2 style={{'textAlign': 'center', 'fontSize': '2.5rem'}}> ON CLOTH.</h2>
                    <main className="unloginIntro">
                        <article className="unloginIntro_">
                            <p className="articleHead">ON CLOTH?</p>
                            <hr/>
                            <p>     . 
                                     
                                      .
                                ON CLOTH    !
                            </p>
                        </article>
                        <article className="unloginFunction">
                            <p className="articleHead">   ?</p>
                            <hr/>
                            <p>
                                   
                                <ul>
                                    <li> </li>
                                    <li>  </li>
                                    <li>   </li>
                                </ul>
                            </p>
                        </article>
                    </main>
                </div>
            }
        </div>
    );
};

export default MainPage;