import Button from "../../base/Button";
import logo from "../../base/.jpg";
import {useState} from "react";
import {Link} from "react-router-dom"
import style from "../style/EditCloth.css";
import TextField from '@mui/material/TextField';

const EditCloth = ({edit, cloth}) => {
    const customCategory = ["1", "2", "3", "4"];
    const [inputImg, setInputImg] = useState('');
    const encodeFileToBase64 = (fileBlob) => {
        const reader = new FileReader();
        reader.readAsDataURL(fileBlob);
        return new Promise((resolve) =>{
            reader.onload= () => {
                setInputImg(reader.result);
                resolve();
            }
        })
    }

    return(
        <div className="addCloth">
            <main className="addClothBox">
                <header className="addClothHeader">
                    <Link to = "/"><img src={logo} alt="logo" /></Link>
                    {true ?
                    <h1 > </h1>:
                    <h1> </h1> 
                    }
                </header>
                <form className="addClothForm">
                    <div className="addClothFormInput">
                        <div className="addClothFormLeft">
                            <div style={{"display": "flex"}} className="imgPreview">
                                {inputImg?
                                    <img style={{"height": "20rem"}} src={inputImg} alt="imgpreview" />
                                    :
                                    <div style={{"height": "10rem", 
                                                "backgroundColor": "beige", 
                                                "display": "flex", 
                                                "justifyContent": "center",
                                                "alignItems": "center",
                                                "fontWeight": 600,
                                                "width": "100%"}}> </div>}
                            </div>
                            <input type="file" placeholder="" onChange={(e) => encodeFileToBase64(e.target.files[0])}/>
                            <div>
                                <select className="addClothSelect" name="" id="">
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select>
                                <select className="addClothSelect" name="" id="">
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select>
                            </div>
                        </div>
                        <div className="addClothFormRight">
                            <input type="text" placeholder="  "/>
                            <textarea name="" id="" cols="20" rows="10" placeholder=" "></textarea>
                            <select className="addClothSelect" name="" id="">
                                <option value="">-- --</option>
                                {customCategory.map((category) => (
                                    <option key={category} value={category}>{category}</option>
                                ))}
                            </select>
                        </div>
                    </div>
                    <div className="addClothFormButton">
                        <Button type="submit"></Button>
                        <Link to = "/"><Button></Button></Link>
                    </div>
                </form>
            </main>
        </div>
    );
}

export default EditCloth;