import Button from "./Button";
import logo from "./.jpg";
import style from "./style/Header.css"
import { Link } from "react-router-dom";
import Logo from "./Logo";

const Header = ({mode}) => {
    const name = "";
    return(
        <div>          
            {mode ? //mode true( )   , false( )    
                <div className="header">
                    <Logo />
                    <form className="header__Search">
                        <input className="header__searchInput" placeholder=" " required />
                        <Button type="submit"></Button> 
                    </form>
                    <div>
                        <span> {name}!</span>
                        <Link to="/login" ><Button></Button></Link>
                    </div>
                    <div>
                        <Link to="/editcloth"><Button> </Button></Link>
                        <Link to="/edituser"><Button> </Button></Link>
                    </div>
                </div> 
             : //  
                <div className="header">
                    <Logo />
                    <div>
                        <Link to="/login"><Button></Button></Link>
                        <Link to="/register"><Button></Button></Link>
                    </div>
                </div>
            }
        <hr />
        </div>
    );
};

export default Header;